// DropThe Charts -- Theme System

export interface Theme {
  name: string;
  bg: string;
  surface: string;
  grid: string;
  textPrimary: string;
  textSecondary: string;
  textSource: string;
  gradients: string[][];  // Array of [startColor, endColor] pairs
  highlightGradient: string[];
  lineStroke: string;
  lineFill: [string, string]; // [topColor, bottomTransparent]
  fontFamily: string;
  monoFamily: string;
  grain: number; // opacity 0-1
}

