// DropThe Charts -- Bar Chart (Vertical)

import { BaseChart, ChartConfig } from '../core/Chart';

export interface BarDataPoint {
  label: string;
  value: number;
  url?: string;
  highlight?: boolean;
}

export interface BarChartConfig extends ChartConfig {
  data: BarDataPoint[];
  valuePrefix?: string;
  valueSuffix?: string;
  showValues?: boolean;
  barRadius?: number;
}

export class BarChart extends BaseChart {
  barConfig: BarChartConfig;

  constructor(container: HTMLElement | string, config: BarChartConfig) {
    super(container, config);
    this.barConfig = { showValues: true, barRadius: 6, ...config };
    this.render();
  }

  render() {
    this.animate((progress) => this.draw(progress));
  }

  draw(progress: number) {
    const { ctx, width, height, theme, padding, barConfig } = this;
    const { data, valuePrefix = '', valueSuffix = '', showValues, barRadius } = barConfig;

    // Clear & background
    this.drawBackground();

    // Title
    this.drawTitle();

    // Chart area
    const chartTop = padding.top + 8;
    const chartBottom = height - padding.bottom - 20;
    const chartLeft = padding.left;
    const chartRight = width - padding.right;
    const chartWidth = chartRight - chartLeft;
    const chartHeight = chartBottom - chartTop;

    // Calculate bar dimensions
    const barCount = data.length;
    const barGap = Math.max(12, chartWidth * 0.08);
    const totalGaps = (barCount - 1) * barGap;
    const barWidth = Math.min(80, (chartWidth - totalGaps) / barCount);
    const totalBarsWidth = barCount * barWidth + totalGaps;
    const startX = chartLeft + (chartWidth - totalBarsWidth) / 2;

    // Find max value
    const maxVal = Math.max(...data.map(d => d.value));
    const scaleY = chartHeight / (maxVal * 1.15); // 15% headroom for labels

    // Draw horizontal grid lines (3 lines)
    ctx.strokeStyle = theme.grid;
    ctx.lineWidth = 1;
    for (let i = 1; i <= 3; i++) {
      const y = chartBottom - (chartHeight * i / 4);
      ctx.beginPath();
      ctx.moveTo(chartLeft, y);
      ctx.lineTo(chartRight, y);
      ctx.stroke();
    }

    // Draw bars
    data.forEach((d, i) => {
      const x = startX + i * (barWidth + barGap);
      const barHeight = d.value * scaleY * progress;
      const y = chartBottom - barHeight;

      // Gradient fill
      const gradColors = d.highlight
        ? theme.highlightGradient
        : theme.gradients[i % theme.gradients.length];
      const grad = this.createGradient(x, y, chartBottom, gradColors);

      // Draw rounded rect bar
      this.roundedRect(x, y, barWidth, barHeight, barRadius || 6, grad);

      // Value label above bar
      if (showValues && progress > 0.5) {
        const labelAlpha = Math.min(1, (progress - 0.5) * 2);
        ctx.globalAlpha = labelAlpha;
        ctx.fillStyle = theme.textPrimary;
        ctx.font = `600 13px ${theme.fontFamily}`;
        ctx.textAlign = 'center';
        const valText = valuePrefix + this.formatNumber(d.value) + valueSuffix;
        ctx.fillText(valText, x + barWidth / 2, y - 10);
        ctx.globalAlpha = 1;
      }

      // Label below bar
      ctx.fillStyle = theme.textSecondary;
      ctx.font = `400 11px ${theme.monoFamily}`;
      ctx.textAlign = 'center';

      // Wrap long labels
      const maxLabelWidth = barWidth + barGap * 0.6;
      const label = this.truncateLabel(d.label, maxLabelWidth);
      ctx.fillText(label, x + barWidth / 2, chartBottom + 16);
    });

    // Baseline
    ctx.strokeStyle = theme.grid;
    ctx.lineWidth = 1;
    ctx.beginPath();
    ctx.moveTo(chartLeft, chartBottom);
    ctx.lineTo(chartRight, chartBottom);
    ctx.stroke();

    // Source
    this.drawSource();
  }

  roundedRect(x: number, y: number, w: number, h: number, r: number, fill: CanvasGradient | string) {
    const { ctx } = this;
    if (h < r * 2) r = h / 2;
    if (h <= 0) return;

    ctx.beginPath();
    ctx.moveTo(x + r, y);
    ctx.lineTo(x + w - r, y);
    ctx.quadraticCurveTo(x + w, y, x + w, y + r);
    ctx.lineTo(x + w, y + h);
    ctx.lineTo(x, y + h);
    ctx.lineTo(x, y + r);
    ctx.quadraticCurveTo(x, y, x + r, y);
    ctx.closePath();
    ctx.fillStyle = fill;
    ctx.fill();
  }

  formatNumber(n: number): string {
    if (n >= 1e9) return (n / 1e9).toFixed(1) + 'B';
    if (n >= 1e6) return (n / 1e6).toFixed(1) + 'M';
    if (n >= 1e3) return (n / 1e3).toFixed(1) + 'K';
    return n.toLocaleString();
  }

  truncateLabel(label: string, maxWidth: number): string {
    const { ctx, theme } = this;
    ctx.font = `400 11px ${theme.monoFamily}`;
    if (ctx.measureText(label).width <= maxWidth) return label;

    // Try splitting at space
    const parts = label.split(' ');
    if (parts.length > 1) {
      // Return first meaningful part
      let short = parts[0];
      if (ctx.measureText(short).width <= maxWidth) return short;
    }

    // Truncate with ellipsis
    let t = label;
    while (t.length > 3 && ctx.measureText(t + '..').width > maxWidth) {
      t = t.slice(0, -1);
    }
    return t + '..';
  }
}
